
import {
  computed,
  defineComponent,
  onMounted,
  reactive,
  ref,
  toRefs,
} from "vue";
import Case from "./components/Case.vue"; // @ is an alias to /src
import Technology from "./components/Technology.vue"; // @ is an alias to /src
import { useRouter } from "vue-router";
const homeData = require("@/json/home.json");

export default {
  name: "Home",
  components: {
    Case,
    Technology,
  },

  setup() {
    const router = useRouter();
    const state = reactive({
      ob: { name: "qqq", age: 10 },
    });
    // 案例
    const cases = computed(() => {
      return homeData.result.cases;
    });

    // 技术
    const techs = computed(() => {
      return homeData.result.technologys;
    });

    onMounted(() => {
      // debugger;
    });

    const moreCase = () => {
      window.scrollTo(0, 0);
      router.push("case");
    };

    const moreTech = () => {
      window.scrollTo(0, 0);
      router.push("technology");
    };

    return {
      cases,
      techs,
      moreCase,
      moreTech,
    };
  },
};
