    TopTool,
  },
  props: {
    data: {
      type: Array,
      default: () => {
        return [];
      },
    },
  },
  setup(prop: any, context: any) {
    /*****************  数据声明  ***************/
    const dataList = reactive([
      { title: "趣生活", type: "APP", icon: "" },
      { title: "趣生活", type: "APP", icon: "" },
      { title: "趣生活", type: "APP", icon: "" },
    ]);

    /*****************  生命周期  ***************/
    /*****************  自定义方法  ***************/
    const getIcon = (icon: string) => {
      return require(`@/assets/case/${icon}.png`);
    };
    const more = () => {
      context.emit("more");
    };
    /*****************  网络请求  ***************/
    return {
      dataList,
      getIcon,
      more,
    };
  },
};
